export default async function fetchData(endpoint, callback) {
  const api =
    process.env.NODE_ENV === "production"
      ? "https://thiago-dev-api.herokuapp.com/api/"
      : "http://localhost:1337/api/";
  const response = await fetch(`${api}${endpoint}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json;charset=utf-8",
      Authorization:
        "Bearer 1622b70128b62a3e0202700f0a08a5d0ce5eeb0ea08021bf6be943baf588600a9f91b364d42eaefaba3442313400ab40542e1cff1403f2ef6217ad4f1adadddf3854c9477a4de1bb250e743b231acae2b522051f305a987bf83c83b5f015a88646bc5d1fb1d9488dc1ce8e6f085de485c877b2862ac2f255d106dd12bcbcd033",
    },
  });
  const resp = await response.json();
  callback(resp?.data);
}
